import { createSlice } from "@reduxjs/toolkit";

let cartSlice = createSlice({
  name: "cart",
  initialState: [],
  reducers: {
    addCount(state, action) {
      let idx = state.findIndex((a) => {
        return a.id === action.payload;
      });
      state[idx].count++;
      //state[action.payload].count++;
    },
    addItem(state, action) {
      state.push(action.payload);
    },
    delCount(state, action) {
      let idx = state.findIndex((a) => {
        return a.id === action.payload;
      });
      state[idx].count--;
      //state[action.payload].count++;
    },
    delItem(state, action) {
      //필터를 사용하여 action.payload와 일치하지 않는 항목들을 새로운 배열로 반환.
      let del = state.filter((a) => {
        return a.id !== action.payload;
      });
      return del; //필터링된 배열을 반환하여 상태를 업데이트
    },
  },
});

export let { addCount, addItem, delCount, delItem } = cartSlice.actions;

export default cartSlice;
